* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.switcher {
  justify-content: center;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
  display: flex;
}

.switcher__btn {
  width: 140px;
  height: 40px;
  text-transform: uppercase;
  color: #f0f8ff;
  cursor: pointer;
  background-color: #116f94;
  border: none;
  border-radius: 10px;
  font-size: 25px;
  position: relative;
  box-shadow: 3px 3px 2px #2d2b2b;
}

.switcher__btn--rigth-indent {
  margin-right: 20px;
}

.switcher__btn:focus, .switcher__btn:hover {
  background-color: #2ea0cd;
}

.switcher__btn:active {
  box-shadow: none;
  bottom: -3px;
  right: -3px;
}

